import { hot } from "react-hot-loader/root";
import React from "react";
import ReactDOM from "react-dom";
import "./index.css";
import NewToDoItem from "./NewToDoItem";
import { Box, Paper, Typography } from "@material-ui/core";
import ToDoList from "./ToDoListViewer";

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      tasks: [],
    };
  }
  pushNewItem = (addedItem) => {
    let tempTasks = this.state.tasks.slice();
    tempTasks.push(addedItem);
    this.setState({ tasks: tempTasks });
  };
  handleDelete = (itemIndex) => {
    let tempTasks = this.state.tasks.slice();
    tempTasks.splice(itemIndex, 1);
    this.setState({ tasks: tempTasks });
  };
  render() {
    return (
      <div className="appContainer">
        <Paper elevation={3} className="todoPaper">
          <Box display="flex" justifyContent="center">
            <Typography
              color="primary"
              variant="h4"
              gutterBottom
              component="div"
            >
              My Todos
            </Typography>
          </Box>
          <NewToDoItem onAdd={this.pushNewItem} />
          <ToDoList
            tasksList={this.state.tasks}
            handleDelete={this.handleDelete}
          />
        </Paper>
      </div>
    );
  }
}

ReactDOM.render(<App />, document.getElementById("root"));

export default hot(App);
